<template>
  <div class="register">
    <div class="register__container">
      <app-title :titleText="'register'"
                 class="title" />
      <form class="register__content"
            @submit.prevent="onSubmit">
        <input type="text"
               placeholder="Email"
               class="content__input email"
               v-model.trim.lazy="email"
               @blur="$v.email.$touch()">
        <input type="password"
               placeholder="Password"
               class="content__input password"
               v-model.trim="password"
               @blur="$v.password.$touch()">
        <input type="password"
               placeholder="Confirm Password"
               class="content__input confirm-password"
               v-model.trim="repeatPassword"
               @blur="$v.repeatPassword.$touch()">
        <div class="alert"
             v-if="$v.$anyError">
          <strong v-if="!$v.email.required">Email is required.</strong>
          <strong v-else-if="!$v.email.email">The email address is badly formatted.</strong>
          <strong v-else-if="!$v.password.required">Password is required.</strong>
          <strong v-else-if="!$v.password.minLength">Password must have at least 6 letters.</strong>
          <strong v-else-if="!$v.password.sameAsPassword">Password and Password Confirmation must match</strong>
        </div>
        <p class="content__text"> Already registered?
          <router-link class="content__link"
                       tag="a"
                       to="/Website-Pizza-Vue/login">Login</router-link>
        </p>
        <button type="submit"
                :disabled="$v.$invalid"
                :class="['content__btn', { 'disabled': $v.$invalid }]">Register</button>
      </form>
      <p class="sub-content"> <strong>OR</strong> </p>
      <div class="sub-button">
        <button type="button"
                class="login-with-google-btn">
          Sign in with Google
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import AppTitle from '@/components/AppTitle.vue'
import { email } from 'vuelidate/lib/validators'
import { required } from 'vuelidate/lib/validators'
import { sameAs } from 'vuelidate/lib/validators'
import { minLength } from 'vuelidate/lib/validators'
export default {
  components: { AppTitle },
  comments: {
    AppTitle
  },
  data() {
    return {
      email: '',
      password: '',
      repeatPassword: ''
    }
  },
  methods: {
    onSubmit() {
      this.email = '',
        this.password = '',
        this.repeatPassword = ''
      this.$store.state.isRegistered = true
      this.$router.push('/Website-Pizza-Vue/menu')
      return true
    }
  },

  validations: {
    email: {
      required,
      email
    },
    password: {
      required,
      minLength: minLength(6)
    },
    repeatPassword: {
      sameAsPassword: sameAs('password')
    }
  }
}
</script>

<style scoped>
body {
  text-align: center;
  padding-top: 2rem;
}

.register {
  display: flex;
  justify-content: center;
  margin-top: 56px;
  min-height: 100vh;
}

.register__container {
  width: 100%;
  height: 100%;
  padding: 0 16px;
}

.title {
  margin-bottom: 24px;
}

.register__content {
  display: flex;
  flex-direction: column;
}

.content__input {
  background-color: #ddd;
  padding: 12px 16px;
  border-radius: 20px;
  border: none;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  transition: .1s;
}

.content__input:hover {
  background-color: #bbb;
}

.content__input:focus {
  background-color: #888;
  outline: none;
}

.content__input:focus::placeholder {
  color: white;
}

.password {
  margin: 12px 0 12px 0;
}

.confirm-password {
  margin-bottom: 12px;
}

.content__text {
  font-weight: 600;
  margin: 0 0 16px 0;
}

.content__btn {
  display: inline-block;
  width: max-content;
  color: #fff;
  background-color: #724cf9;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  font-size: large;
  line-height: 1.5;
  cursor: pointer;
  transition: .15s ease-in-out;
}

.content__btn:hover {
  background-color: #5a2ff3;
  filter: drop-shadow(0 0 8px #724cf9);
}

.sub-content {
  text-align: center;
  margin: 0;
  padding: 0;
}

strong {
  font-weight: bolder;
}

.sub-button {
  display: flex;
  justify-content: center;
  margin: 16px 0;
}

.login-with-google-btn {
  transition: background-color .3s, box-shadow .3s;
  margin: 0 auto;
  padding: 12px 16px 12px 16px;
  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 1px 1px rgba(0, 0, 0, .25);
  color: #757575;
  font-size: 14px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;
  max-width: 220px;
  width: 100%;
}

.login-with-google-btn:hover {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .25);
}

.login-with-google-btn:active {
  background-color: #eeeeee;
}

.login-with-google-btn:focus {
  outline: none;
  box-shadow:
    0 -1px 0 rgba(0, 0, 0, .04),
    0 2px 4px rgba(0, 0, 0, .25),
    0 0 0 3px #c8dafc;
}

.alert {
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  padding: 1rem 1rem;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  margin-top: 12px;
}

.disabled {
  opacity: 0.7;
}

@media screen and (min-width: 576px) {
  .register__container {
    width: 540px;
  }
}

@media screen and (min-width:768px) {
  .register__container {
    width: 720px;
  }
}

@media screen and (min-width:992px) {
  .register__container {
    width: 960px;
  }
}

@media screen and (min-width:1200px) {
  .register__container {
    width: 1140px;
  }
}

@media screen and (min-width:1400px) {
  .register__container {
    width: 1320px;
  }
}
</style>