<script>

export default {
  data() {
    return {
      isShow: true,
    }
  },

  methods: {
    onload() {
      setTimeout(() => { this.isShow = false }, 1000)
    }
  },

  mounted() {
    this.onload()
    //   document.onreadystatechange = () => {
    // if (document.readyState == "complete") {
    //   console.log('Page completed with image and files!')
  }
}


