<style scoped>
.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  background: rgb(0, 5, 31);
  color: white;
}

h1 {
  animation: lighting 0.5s infinite;
  animation-direction: alternate;
}

.preloader__img {
  position: relative;
  width: 120px;
  height: auto;
  margin-right: 20px;
  animation: lighting 0.5s infinite, move 2s infinite;
}

@keyframes lighting {
  from {
    filter: drop-shadow(0 0 0px white);
  }

  to {
    filter: drop-shadow(0 0 10px white);
  }
}

@keyframes move {
  0% {
    top: -400px;
  }

  50% {
    top: 0;
  }

  100% {
    top: 400px;
  }
}
</style>